import React, {useState, useEffect} from 'react'
import mahasiswaService from './services/mahasiswaService'

const Filter = ({query, setQuery}) => {
    const queryHandler = event => {
        setQuery(event.target.value);
    }

    return(
        <div>
            <input className="form-control" style={{width: "50%"}} value={query} onChange={queryHandler} placeholder='Ketik nama atau NIM...'/>
        </div>
    )
}

const Mahasiswa = ({nama, nim, jurusan}) => {
    //console.log(counter)
    return(
        <>
          <tr>
            <td style={{width: "35%"}}>{nama}</td>
            <td style={{width: "32.5%"}}>{nim}</td>
            <td style={{width: "32.5%"}}>{jurusan}</td>
          </tr>
        </> 
    )
}

const ResultTable = ({mahasiswa}) => {
    if(mahasiswa.length !== 0){
        //console.log(mahasiswa.length)
        return (
            <div>
                <br/>
                <table className="table table-sm table-hover">
                    <thead className="thead-dark">
                        <tr>
                            <th style={{width: "35%"}}>Nama</th>
                            <th style={{width: "32.5%"}}>NIM</th>
                            <th style={{width: "32.5%"}}>Jurusan</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            //console.log(mahasiswa),
                            mahasiswa.map((m) => <Mahasiswa nama={m.Nama} nim={m.NIM} jurusan={m.Jurusan} key={m.NIM}/>)
                        }
                    </tbody>
                </table>
            </div>
        )
    }
    else{
        return(
            <>
            </>
        )
    }
}


const Header = () => {
    return(
        <div>
            <h3>ITB NIM Finder</h3>
            <h6>by Surya Mahardika </h6>
        </div>
    )
}

const App = () => {
    const [query, setQuery] = useState('')
    const [mahasiswa, setMahasiswa] = useState([])
    //console.log(query)

    useEffect(() => {
        mahasiswaService.getMahasiswaByQuery(query).then(m => {
            setMahasiswa(m);
        })
    }, [query])

    return(
        <div className="container">
            <Header/>
            <Filter query={query} setQuery={setQuery}/>
            <ResultTable mahasiswa={mahasiswa}/>
        </div>
    )
}

export default App